import React, { Component } from 'react';
import {
	BrowserRouter as Router,
	Route,
	Link
} from 'react-router-dom';


//components
import Header from './Components/headerComponent/header';
import Footer from './Components/footerComponent/footer';
import Homepage from './Components/pages/homePage';
import About from './Components/pages/about';
import Links from './Components/pages/links';

//includes
import './Assets/CSS/default.css';

/*
	Uses router to link to what container should be displayed, uses a single web-page (REACT) instead of anchor tags loading new webpages, which means it loads faster
	
*/


class App extends Component{
render() {
	  return (
		  <Router basename={process.env.PUBLIC_URL}>
			  <div className="App">
      
				  <Header />
					<Route exact path='/ ' component={Homepage} />
					<Route exact path='/' component={Homepage} />
					<Route exact path='/Roland_Smith_Resume_2019.doc' component={About} />
					<Route exact path='/About' component={About} />
					<Route exact path='/Links' component={Links} />
				  <Footer />
			  </div>
		  </Router>
	  );
	  }
}

export default App;
